.list-group {
	margin-top: 30px;
}
.list-group-item {
	padding: 15px 20px;
}

.list-group-item {
	span {
		line-height: 35px;
		font-size: 20px;
		cursor: pointer;
		width: 100%;
	}
	&:hover {
		-webkit-box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.1);
		-moz-box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.1);
		box-shadow: 0px 0px 10px 5px rgba(0, 0, 0, 0.1);
	}
}

.list-group-item {
	input {
		line-height: 35px;
		font-size: 20px;
		text-align: center;
		border: 0;
	}
}

.list-group-item {
	button {
		width: 35px;
		height: 35px;
		margin: 3px;
		font-size: 17px;
		border: none;
		cursor: pointer;
	}
}

.list-group-item {
	.btn-cookie {
		color: #e09f3e;
	}
}

.list-group-item {
	.btn-trash {
		color: #ff5356;
	}
}
.list-group-item {
	.fa-star {
		width: 35px;
		height: 35px;
		text-align: center;
		line-height: 35px;
		font-size: 16px;
		color: #ffd700;
		transition: 0.3s all;
		transform: translateX(30px);
		opacity: 0;
	}
}

.list-group-item.like {
	.fa-star {
		opacity: 1;
		transform: translateX(0px);
	}
}

.list-group-item.increase .list-group-item-label,
.list-group-item.increase .list-group-item-input {
	color: #e09f3e;
}

.list-group-item.increase {
	.btn-star {
		color: #aeaeae;
	}
}
